div {
    flex-basis: content;
    align-self: center;
    flex-grow: 1;
}

header {
    font-style: italic;
    display: flex;

    min-height: 100px;
}

.left {
    flex: none;
}

ul.navigation {
    align-items: flex-start;
    padding-inline-start: 0px;
}

li.navigation {
    display: inline-flex;
    list-style: none;
}

li.navigation-active {
    display: inline-flex;
    list-style: none;
    border-bottom: var(--accent);
    border-bottom-style: outset;
    transition: all linear 0.1s;
}
